const InfiniteGallery = () => {
  let mainElement
  let options
  let width = 0,
    height = 0
  let bodyElement

  let imageItems = []

  const mount = (
    element,
    mountOptions = ({
      itemElementClassName = ".image",
      gap = 30,
      itemsX = 4,
      itemsY = 4,
    } = {})
  ) => {
    mainElement = document.querySelector(element)
    mainElement.classList.add("infinite-gallery")
    bodyElement = mainElement.querySelector(".infinite-gallery-body")
    let items = mainElement.querySelectorAll(mountOptions.itemElementClassName)
    let itemWidth = mainElement.querySelector(mountOptions.itemElementClassName)
      .clientWidth
    let itemHeight = mainElement.querySelector(
      mountOptions.itemElementClassName
    ).clientHeight
    width =
      itemWidth * mountOptions.itemsX +
      (mountOptions.itemsX - 1) * mountOptions.gap
    height =
      itemHeight * mountOptions.itemsY +
      (mountOptions.itemsY - 1) * mountOptions.gap

    bodyElement.style.width = `${width}px`
    bodyElement.style.height = `${height}px`

    items.forEach((item, index) => {
      let width = item.clientWidth
      let height = item.clientHeight
      item.style.position = "absolute"
      let indexY = Math.floor(index / mountOptions.itemsX)
      let indexX = index % mountOptions.itemsX
      let gapX = indexX === 0 ? 0 : mountOptions.gap * indexX
      let gapY = indexY === 0 ? 0 : mountOptions.gap * indexY
      item.style.top = `${indexY * height + gapY}px`
      item.style.left = `${indexX * width + gapX}px`
      imageItems.push({
        x: indexY * height + gapY,
        y: indexX * width + gapX,
        element: item,
      })
    })

    options = mountOptions

    makeDummies()
    init()
  }

  const init = () => {
    dragInit()
  }

  const makeDummies = () => {
    const xTimes = Math.ceil(window.innerWidth / width)
    const yTimes = Math.ceil(window.innerHeight / height)
    for (let i = 0; i < xTimes; i++) {
      let cloneBodyElement = bodyElement.cloneNode(true)
      cloneBodyElement.classList.add("infinite-gallery-dummy")
      cloneBodyElement.style.left = `-${(width + options.gap) * (i + 1)}px`
      cloneBodyElement.style.top = `0px`
      mainElement.appendChild(cloneBodyElement)
    }
    for (let i = 0; i < xTimes; i++) {
      let cloneBodyElement = bodyElement.cloneNode(true)
      cloneBodyElement.classList.add("infinite-gallery-dummy")
      cloneBodyElement.style.left = `${(width + options.gap) * (i + 1)}px`
      cloneBodyElement.style.top = `0px`
      mainElement.appendChild(cloneBodyElement)
    }
    for (let i = 0; i < yTimes; i++) {
      let cloneBodyElement = bodyElement.cloneNode(true)
      cloneBodyElement.classList.add("infinite-gallery-dummy")
      cloneBodyElement.style.left = `0px`
      cloneBodyElement.style.top = `${(height + options.gap) * (i + 1)}px`
      mainElement.appendChild(cloneBodyElement)
    }
    for (let i = 0; i < yTimes; i++) {
      let cloneBodyElement = bodyElement.cloneNode(true)
      cloneBodyElement.classList.add("infinite-gallery-dummy")
      cloneBodyElement.style.left = `0px`
      cloneBodyElement.style.top = `-${(height + options.gap) * (i + 1)}px`
      mainElement.appendChild(cloneBodyElement)
    }
    for (let i = 0; i < yTimes; i++) {
      for (let j = 0; j < xTimes; j++) {
        let cloneBodyElement = bodyElement.cloneNode(true)
        cloneBodyElement.classList.add("infinite-gallery-dummy")
        cloneBodyElement.style.left = `${(width + options.gap) * (j + 1)}px`
        cloneBodyElement.style.top = `-${(height + options.gap) * (i + 1)}px`
        mainElement.appendChild(cloneBodyElement)
      }
      for (let j = 0; j < xTimes; j++) {
        let cloneBodyElement = bodyElement.cloneNode(true)
        cloneBodyElement.classList.add("infinite-gallery-dummy")
        cloneBodyElement.style.left = `${(width + options.gap) * (j + 1)}px`
        cloneBodyElement.style.top = `${(height + options.gap) * (i + 1)}px`
        mainElement.appendChild(cloneBodyElement)
      }
      for (let j = 0; j < xTimes; j++) {
        let cloneBodyElement = bodyElement.cloneNode(true)
        cloneBodyElement.classList.add("infinite-gallery-dummy")
        cloneBodyElement.style.left = `-${(width + options.gap) * (j + 1)}px`
        cloneBodyElement.style.top = `-${(height + options.gap) * (i + 1)}px`
        mainElement.appendChild(cloneBodyElement)
      }
      for (let j = 0; j < xTimes; j++) {
        let cloneBodyElement = bodyElement.cloneNode(true)
        cloneBodyElement.classList.add("infinite-gallery-dummy")
        cloneBodyElement.style.left = `-${(width + options.gap) * (j + 1)}px`
        cloneBodyElement.style.top = `${(height + options.gap) * (i + 1)}px`
        mainElement.appendChild(cloneBodyElement)
      }
    }
  }

  let mouseDown = {
    down: false,
    x: 0,
    y: 0,
  }

  const reposition = (x, y) => {
    mainElement.style.top = `${y}px`
    mainElement.style.left = `${x}px`
  }

  function dragInit() {
    mainElement.addEventListener(
      "mousedown",
      (e) => {
        mouseDown.down = true
        mouseDown.x = mainElement.offsetLeft - e.clientX
        mouseDown.y = mainElement.offsetTop - e.clientY
        mainElement.style.cursor = "grabbing"
      },
      true
    )
    mainElement.addEventListener(
      "mousemove",
      (e) => {
        const { clientX, clientY } = e
        if (mouseDown.down) {
          let x = mouseDown.x + clientX
          let y = mouseDown.y + clientY
          if(y >= window.innerHeight) {
            y = window.innerHeight - height
            mouseDown.x = mainElement.offsetLeft - e.clientX
            mouseDown.y = mainElement.offsetTop - e.clientY
          }
          if(y <= -height) {
            y = 0
            mouseDown.x = mainElement.offsetLeft - e.clientX
            mouseDown.y = mainElement.offsetTop - e.clientY
          }
          if(x <= -width) {
            x = 0
            mouseDown.x = mainElement.offsetLeft - e.clientX
            mouseDown.y = mainElement.offsetTop - e.clientY
          }
          if(x >= window.innerWidth) {
            x = window.innerWidth - width
            mouseDown.x = mainElement.offsetLeft - e.clientX
            mouseDown.y = mainElement.offsetTop - e.clientY
          }
          reposition(x, y)
        }
      },
      true
    )
    function resetMouseDown() {
      if (!mouseDown.down) return
      mouseDown.down = false
      mainElement.style.cursor = ""
    }
    window.addEventListener("mouseup", resetMouseDown, true)
  }

  return {
    mount,
  }
}

export default InfiniteGallery()
